




















































































































































































import { Vue, Component, Watch } from 'vue-property-decorator'
import TokenBalanceCard from '../components/TokenBalanceCard.vue'
import WalletCard from '../components/WalletCard.vue'
import BigNumber from 'bignumber.js'
import Store from '../store'
@Component({
    components: {
        TokenBalanceCard,
        WalletCard
    }
})
export default class Transfer extends Vue {
    private from: string = ''
    private unit: string = 'VET'
    private token!: app.Token
    private showTl = false
    private showWl = false
    private form: any
    private wallet: app.Wallet | null = null
    private showImport = false
    private doImport = false
    private walletListTemp: app.Wallet[] = []
    private tempFrom?: string

    public beforeCreate() {
        this.form = this.$form.createForm(this)
    }

    public async importWallet() {
        await this.$store.dispatch('importWallet')
        this.setWalletList()
    }

    public async setWalletList(): Promise<void> {
        const result: app.Wallet[] = []
        for (const item of this.$store.getters.wallets) {
            if ((await connex.vendor.owned(item.address)) === true) {
                result.push(item)
            }
        }
        this.walletListTemp = result
        return
    }

    get walletList() {
        return this.walletListTemp
            .map((item: app.Wallet) => {
                return {
                    ...item,
                    balance:
                        (this.$store.getters.balanceList &&
                            this.$store.getters.balanceList[item.address] &&
                            this.$store.getters.balanceList[item.address][
                                this.unit
                            ]) ||
                        0
                }
            })
            .sort(
                (
                    a: app.Wallet & { balance: number },
                    b: app.Wallet & { balance: number }
                ) => {
                    return b.balance - a.balance
                }
            )
    }

    get SetAmountStyle() {
        return this.unit && this.unit.toLowerCase() === 'vtho'
            ? { cursor: 'not-allowed' }
            : ''
    }

    get toList() {
        return this.$store.state.toList.map((item: string) => {
            return Vue.filter('toChecksumAddress')(item)
        })
    }

    public checkAmount(rule: any, value: any, callback: any) {
        let msg = ''
        if (!value) {
            callback()
        }
        if (!Vue.filter('balanceCheck')(value)) {
            msg = this.$t('msg.amount_invalid').toString()
            callback(new Error(msg))
        }
        const v = new BigNumber(value)
        const b = new BigNumber(this.tokenBalance)

        if (v.isGreaterThan(b)) {
            msg = this.$t('msg.amount_not_enough').toString()
            callback(new Error(msg))
        }
        callback()
    }

    public walletChange(wallet: app.Wallet) {
        const el = this.$refs.walletList as HTMLElement
        this.wallet = wallet
        this.from = wallet.address
        if (this.tempFrom) {
            this.$store.commit('deleteTemp', this.tempFrom)
            this.tempFrom = ''
        }
        this.form.setFieldsValue({ from: wallet.address })
        this.showWl = false
        this.showImport = false
        el.scrollTop = 0
    }

    public tokenChange(symbol: string) {
        const el = this.$refs.tokenList as HTMLElement
        this.token = this.tokenlist.find((item: app.Token) => {
            return item.symbol === symbol
        })
        this.unit = symbol
        this.showTl = false
        this.form.resetFields(['val'])
        if (el) {
            el.scrollTop = 0
        }
    }

    public setAmount() {
        if (this.unit && this.unit.toLowerCase() === 'vtho') {
            return
        }
        this.form.setFieldsValue({ val: this.tokenBalance })
    }

    get balances() {
        return (
            (this.$store.getters.balanceList &&
                this.$store.getters.balanceList[this.from]) ||
            {}
        )
    }

    get tokenBalance() {
        return (
            (this.$store.getters.balanceList &&
                this.$store.getters.balanceList[this.from] &&
                this.$store.getters.balanceList[this.from][this.unit]) ||
            0
        )
    }

    get tokenlist() {
        return this.$store.getters.tokens.map((item: app.Token) => {
            return {
                ...item,
                balance: this.balances[item.symbol] || 0
            }
        })
    }

    public async created() {
        this.$ga.page('/tokens/transfer')
        this.initForm()
    }

    public send(e: Event) {
        e.preventDefault()
        this.form.validateFields(async (err: any, val: any) => {
            const symbolUnit = this.unit
            const isVet = (symbolUnit! as string).toLowerCase() === 'vet'
            let decimals = 18
            if (!err) {
                let result: any
                try {
                    if (isVet) {
                        result = await this.transferVet(
                            val.from,
                            val.to,
                            val.val,
                            18
                        )
                    } else {
                        const temp = this.$store.getters.tokens.find(
                            (item: app.Token) => {
                                return (
                                    item.symbol.toString().toLowerCase() ===
                                    symbolUnit.toString().toLowerCase()
                                )
                            }
                        )
                        decimals = temp.decimals
                        result = await this.tokenTransfer(
                            val.to,
                            val.from,
                            temp.address,
                            val.val,
                            symbolUnit,
                            decimals
                        )
                    }
                } catch (error) {
                    this.$error({
                        class: 'cus-modal',
                        title: this.$createElement(
                            'span',
                            {
                                style: {
                                    color: '#fff'
                                }
                            },
                            error.message
                        )
                    })
                    return
                }
                this.$store.dispatch('addTransferLog', {
                    txid: result.txid,
                    from: val.from,
                    to: val.to,
                    amount: Vue.filter('valToHex')(val.val, decimals),
                    coin: symbolUnit
                })
                this.$success({
                    title: this.$createElement(
                        'span',
                        {
                            style: {
                                color: '#fff'
                            }
                        },
                        this.$t('transfer.tx_success').toString()
                    ),
                    class: 'cus-modal',
                    content: this.$createElement('div', {
                        style: {
                            color: '#fff'
                        },
                        domProps: {
                            innerHTML: this.$t('transfer.tx_info', {
                                txid: result.txid,
                                path: 'https://insight.vecha.in/#/txs/'
                            }).toString()
                        }
                    }),
                    okType: 'primary',
                    maskStyle: { background: 'transparent' },
                    okButtonProps: { props: { ghost: true } },
                    maskClosable: true
                } as any)
                if (this.doImport) {
                    this.$store.dispatch('importWallet', this.from)
                }
                this.resetForm()
            }
        })
    }

    public resetForm() {
        this.form.resetFields(['to', 'val', 'from'])
        this.wallet = null
        this.doImport = false
        this.showImport = false
        this.from = ''
    }

    public async transferVet(
        from: string,
        to: string,
        amount: number,
        decimals: number
    ) {
        const svc = connex.vendor.sign('tx')
        return await svc.signer(from).request([
            {
                to,
                value: Vue.filter('valToHex')(amount, decimals),
                data: '0x',
                comment: `Transfering ${amount} VET`
            }
        ])
    }

    public async tokenTransfer(
        to: string,
        from: string,
        address: string,
        amount: number,
        symbol: string,
        decimals: number
    ) {
        const abi = {
            constant: false,
            inputs: [
                {
                    name: '_to',
                    type: 'address'
                },
                {
                    name: '_value',
                    type: 'uint256'
                }
            ],
            name: 'transfer',
            outputs: [
                {
                    name: '',
                    type: 'bool'
                }
            ],
            payable: false,
            stateMutability: 'nonpayable',
            type: 'function'
        }
        const method = connex.thor.account(address).method(abi)
        const transferClause = method.asClause(
            to,
            Vue.filter('valToHex')(amount, decimals)
        )
        const svc = connex.vendor.sign('tx')
        svc.signer(from)
        return await svc
            .comment('')
            .request([
                {
                    ...transferClause,
                    comment: `Transfering ${amount} ${symbol}`
                }
            ])
    }

    beforeDestroy() {
        if (this.tempFrom) {
            this.$store.commit('deleteTemp', this.tempFrom)
        }
    }

    public async initForm() {
        const token = this.tokenlist.find((item: app.Token) => {
            return (
                item.symbol.toLowerCase() ===
                (this.$route.query.symbol || 'VET').toString().toLowerCase()
            )
        })
        if (token) {
            this.unit = token.symbol
            this.token = token
        } else {
            this.token = this.tokenlist[0]
        }
        await this.setWalletList()
        const wallet = this.walletList!.find(item => {
            return (
                item.address.toLowerCase() ===
                (this.$route.query.from || '').toString().toLowerCase()
            )
        })

        if (this.$route.query && this.$route.query.from) {
            if (await connex.vendor.owned(this.$route.query.from.toString())) {
                if (wallet) {
                    this.from = wallet.address
                    this.form.setFieldsValue({ from: this.from })
                    this.wallet = wallet
                } else {
                    this.showImport = true
                    this.tempFrom = this.$route.query.from.toString().toLowerCase()
                    this.from = this.$route.query.from.toString().toLowerCase()
                    Store.setBalanceByAddress(this.tempFrom)
                    this.$store.commit('addTempAddress', this.tempFrom)
                    this.wallet = {
                        name: this.$t('wallets.unknown_wallet').toString(),
                        address: this.from
                    }
                }
            }
        }
    }
}
